// Get access token
var getToken = $.parseJSON($.ajax({
    url: 'https://store.nvidia.com/store/nvidia/SessionToken?apiKey=9485fa7b159e42edb08a83bde0d83dia&format=json',
    dataType: 'json',
    async: false
}).responseText);
var accessToken = getToken.access_token;
console.log(accessToken);

// Post access token
$.ajax({
  type: "POST",
  url: 'https://api.digitalriver.com/v1/shoppers/me/carts/active/line-items?format=json&method=post&productId=3181782008&token=newly%20generatedtoken&quantity=1&callback=jQuery1102021175939533911248_1463555793392&_=1463555793396',
  data: accessToken,
  success: null,
  dataType: 'json'
});

// Checkout url
var checkout = 'https://api.digitalriver.com/v1/shoppers/me/carts/active/web-checkout?token=' + accessToken;
$('.button').attr('href', checkout)